export function useFertilizationInit() {
    const store = useStore();
    const router = useRouter();

    const showGrid = ref(false);
    const problem = ref(false);
    const isBornCycle = computed(() => store.getters["artCycle/isBornCycle"]);
    const embryoDetailDialog = ref(false);
    const openEmbryoDetailDialog = () => (embryoDetailDialog.value = true);
    const closeEmbryoDetailDialog = () => (embryoDetailDialog.value = false);

    const getFertilization = () => {
        if (store.state.artCycle.selectedCycle == null) {
            problem.value = true;
            return;
        }
        emitter.emit("app:show-page-loading", true);

        store
            .dispatch(
                "fertilization/getFertilization",
                store.state.artCycle.selectedCycle.cycleId
            )
            .then(() => {
                showGrid.value = true;
            })
            .catch(() => {
                router.push("/fertilization/create");
                showGrid.value = false;
            })
            .finally(() => {
                emitter.emit("app:show-page-loading", false);
            });
    };

    return {
        store,
        isBornCycle,
        problem,
        showGrid,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog,
        getFertilization
    };
}

export function useFertilizationGridInit() {
    const store = useStore();

    const fertilizationGrid = computed(
        () => store.state.fertilization.fertilizationLines
    );
    const createFertilizationLineDialog = ref(false);

    const fertilizationGridDays = reactive([
        { day: 0, date: null, editable: false },
        { day: 1, date: null, editable: false },
        { day: 2, date: null, editable: false },
        { day: 3, date: null, editable: false },
        { day: 4, date: null, editable: false },
        { day: 5, date: null, editable: false },
        { day: 6, date: null, editable: false },
        { day: 7, date: null, editable: false }
    ]);

    const fillDaysRow = (val) => {
        fertilizationGridDays[0].date = val.fertDate0;
        fertilizationGridDays[1].date = val.fertDate1;
        fertilizationGridDays[2].date = val.fertDate2;
        fertilizationGridDays[3].date = val.fertDate3;
        fertilizationGridDays[4].date = val.fertDate4;
        fertilizationGridDays[5].date = val.fertDate5;
        fertilizationGridDays[6].date = val.fertDate6;
        fertilizationGridDays[7].date = val.fertDate7;
        getFertilizationLines(val.fertId);
    };

    const embryoDetailDialog = inject("embryoDetailDialog");

    const openEmbryoDetailDialog = inject("openEmbryoDetailDialog");
    const closeEmbryoDetailDialog = inject("closeEmbryoDetailDialog");

    const createFetilizationRow = () => {
        createFertilizationLineDialog.value = true;
    };

    const getFertilizationLines = (fertId) => {
        store
            .dispatch("fertilization/getFertilizationLines", fertId)
            .then(() => {})
            .catch(() => {})
            .finally(() => {});
    };

    return {
        store,
        fertilizationGrid,
        createFetilizationRow,
        createFertilizationLineDialog,
        fertilizationGridDays,
        getFertilizationLines,
        embryoDetailDialog,
        openEmbryoDetailDialog,
        closeEmbryoDetailDialog,
        fillDaysRow
    };
}

