export function useCraeteFertilizationFromInit() {
    const store = useStore();
    const router = useRouter();

    const selectedCycle = computed(() => store.state.artCycle.selectedCycle);

    const form = reactive({
        fertDate0: null,
        fertTime0: null,
        fertInit0: null,

        fertDate1: null,
        fertTime1: null,
        fertInit1: null,

        fertDate2: null,
        fertTime2: null,
        fertInit2: null,

        fertDate3: null,
        fertTime3: null,
        fertInit3: null,

        fertDate5: null,
        fertTime5: null,
        fertInit5: null,

        fertDate6: null,
        fertTime6: null,
        fertInit6: null,

        fertDate7: null,
        fertTime7: null,
        fertInit7: null,

        comment: null,
        expComment: null
    });

    const rules = {
        fertDate0: { required },
        fertDate1: { required },
        fertDate2: { required },
        fertDate3: { required },
        fertDate4: { required },
        fertDate5: { required },
        fertDate6: { required },
        fertDate7: { required }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const createFertilization = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        let formData = {
            cycleId: selectedCycle.value.cycleId,
            patientId: selectedCycle.value.patientId,
            ...form,
            fertDate0: form.fertDate0
                ? format(form.fertDate0, DATE_FORMAT_STRING)
                : null,
            fertDate1: form.fertDate1
                ? format(form.fertDate1, DATE_FORMAT_STRING)
                : null,
            fertDate2: form.fertDate2
                ? format(form.fertDate2, DATE_FORMAT_STRING)
                : null,
            fertDate3: form.fertDate3
                ? format(form.fertDate3, DATE_FORMAT_STRING)
                : null,
            fertDate4: form.fertDate4
                ? format(form.fertDate4, DATE_FORMAT_STRING)
                : null,
            fertDate5: form.fertDate5
                ? format(form.fertDate5, DATE_FORMAT_STRING)
                : null,
            fertDate6: form.fertDate6
                ? format(form.fertDate6, DATE_FORMAT_STRING)
                : null,
            fertDate7: form.fertDate7
                ? format(form.fertDate7, DATE_FORMAT_STRING)
                : null
        };
        store
            .dispatch("fertilization/createFertlization", formData)
            .then(() => {
                router.push("/fertilization");
            })
            .catch(() => {})
            .finally(() => {});
    };

    const populateDates = (newValue) => {
        if (!form.fertDate1) {
            form.fertDate1 = addDays(form.fertDate0, 10);
        }
        if (!form.fertDate2) {
            form.fertDate2 = addDays(form.fertDate0, 10 * 2);
        }
        if (!form.fertDate3) {
            form.fertDate3 = addDays(form.fertDate0, 10 * 3);
        }
        if (!form.fertDate4) {
            form.fertDate4 = addDays(form.fertDate0, 10 * 4);
        }
        if (!form.fertDate5) {
            form.fertDate5 = addDays(form.fertDate0, 10 * 5);
        }
        if (!form.fertDate6) {
            form.fertDate6 = addDays(form.fertDate0, 10 * 6);
        }
        if (!form.fertDate7) {
            form.fertDate7 = addDays(form.fertDate0, 10 * 7);
        }
    };

    return { form, createFertilization, populateDates, submitted, v$ };
}

export function useFertilizationLineCreateDialog() {
    const store = useStore();

    const createFertilizationLineDialog = inject(
        "createFertilizationLineDialog"
    );
    const loading = ref(false);

    const form = reactive({
        fate: null,
        strawId: null,
        frozenDate: null,
        thawingDate: null,
        comment: null
    });

    const fateOptions = reactive([
        "Frozen",
        "Discard",
        "Culture",
        "Donate",
        "Thawed",
        "Transfer",
        "Sent Out"
    ]);

    const save = () => {
        let fertilization = store.state.fertilization.fertilization;
        const formData = {
            lineId: 0,
            fertilizationId: fertilization.fertId,
            cycleId: fertilization.cycleId,
            noEmbryo: store.state.fertilization.fertilizationLines.length,
            ...form,
            frozenDate: form.frozenDate
                ? format(form.frozenDate, DATE_FORMAT_STRING)
                : null,
            thawDate: form.thawingDate
                ? format(form.thawingDate, DATE_FORMAT_STRING)
                : null
        };
        loading.value = true;
        store
            .dispatch("fertilization/createFertilizationLine", formData)
            .then(() => {})
            .catch(() => {
                console.log("err");
            })
            .finally(() => {
                createFertilizationLineDialog.value = false;
                loading.value = false;
            });
    };

    const emptyForm = () => {
        form.fate = null;
        form.strawId = null;
        form.frozenDate = null;
        form.thawingDate = null;
        form.comment = null;
    };

    return {
        form,
        fateOptions,
        save,
        loading,
        emptyForm,
        createFertilizationLineDialog
    };
}
